body {
  background-color: #f7f9fc;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

.chat-container {
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
}

.chat-container h2 {
  text-align: center;
  background-color: #0000;
  margin: 0 0 1rem;
  padding: 0;
}

.chat-box {
  background-color: #fafafa;
  border: 1px solid #ddd;
  border-radius: 8px;
  flex: auto;
  max-height: 500px;
  margin-bottom: 1rem;
  padding: 1rem;
  overflow-y: auto;
}

.message-pair {
  margin-bottom: 1rem;
}

.user-msg {
  text-align: right;
  background-color: #dceeff;
  border-radius: 8px;
  margin: 5px 0;
  padding: 10px;
}

.bot-msg {
  text-align: left;
  background-color: #f0f0f0;
  border-radius: 8px;
  margin: 5px 0;
  padding: 10px;
}

.loading {
  color: #999;
  text-align: center;
  margin-top: 10px;
  font-style: italic;
}

.chat-form {
  background: #fafafa;
  border-top: 1px solid #ddd;
  flex-direction: column;
  gap: .75rem;
  padding: .5rem 1rem 1rem;
  display: flex;
}

.chat-input {
  border: 1px solid #ccc;
  border-radius: 6px;
  width: 100%;
  padding: .75rem;
  font-size: 1rem;
}

.chat-form-buttons {
  justify-content: flex-end;
  gap: .5rem;
  display: flex;
}

.chat-form-buttons button {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: .75rem 1rem;
  font-size: 1rem;
  transition: background-color .2s;
}

.chat-form-buttons button[type="submit"] {
  color: #fff;
  background-color: #007bff;
}

.chat-form-buttons button[type="submit"]:hover:not(:disabled) {
  background-color: #0056b3;
}

.chat-form-buttons button:disabled {
  cursor: not-allowed;
  background-color: #a1c9f1;
}

.chat-form-buttons button[type="button"] {
  color: #fff;
  background-color: #f44336;
}

.chat-form-buttons button[type="button"]:hover {
  background-color: #d32f2f;
}

.branding {
  color: #888;
  text-align: left;
  margin-top: .5rem;
  font-size: .75rem;
}

.faq-divider {
  color: #555;
  margin: 12px 0 6px;
  padding-left: 4px;
  font-size: 14px;
  font-weight: bold;
}

.faq-buttons {
  flex-direction: column;
  gap: 8px;
  margin: 16px 0;
  display: flex;
}

.faq-button {
  cursor: pointer;
  text-align: left;
  background-color: #f5f5f5;
  border: none;
  border-radius: 6px;
  padding: 10px 14px;
  font-size: 15px;
  transition: background-color .2s;
}

.faq-button:hover {
  background-color: #e0e0e0;
}

.chat-widget-wrapper {
  z-index: 1000;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  display: flex;
  position: fixed;
  bottom: 24px;
  right: 24px;
}

.chat-widget-popup {
  width: 380px;
  height: 600px;
  overflow: unset;
  background: #fff;
  border-radius: 16px;
  flex-direction: column;
  animation: .3s ease-out slideUp;
  display: flex;
  box-shadow: 0 12px 32px #0000001f;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.chat-box {
  flex: auto;
  padding: 8px;
  overflow-y: auto;
}

.chat-form {
  background: #fafafa;
  border-top: 1px solid #eee;
  flex: none;
  padding: 8px;
}

.chat-bubble {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #2d3b8f, #4e5dbb);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 64px;
  font-size: 1.6rem;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  box-shadow: 0 8px 24px #0003;
}

.chat-bubble:hover {
  transform: scale(1.1);
  box-shadow: 0 10px 32px #0000004d;
}
