/* App.css */
body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f7f9fc;
  margin: 0;
  padding: 0;
}

.chat-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.chat-container h2 {
  margin: 0 0 1rem;
  text-align: center;
  padding: 0;
  background-color: transparent;
}

.chat-box {
  flex: 1 1 auto;
  max-height: 500px;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  background-color: #fafafa;
  overflow-y: auto;
  margin-bottom: 1rem;
}

.message-pair {
  margin-bottom: 1rem;
}

.user-msg {
  text-align: right;
  background-color: #dceeff;
  padding: 10px;
  border-radius: 8px;
  margin: 5px 0;
}

.bot-msg {
  text-align: left;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 8px;
  margin: 5px 0;
}

.loading {
  font-style: italic;
  color: #999;
  text-align: center;
  margin-top: 10px;
}

/* FORM AT BOTTOM OF POPUP */
.chat-form {
  display: flex;
  flex-direction: column; /* input over buttons */
  gap: 0.75rem;
  padding: 0.5rem 1rem 1rem;
  background: #fafafa;
  border-top: 1px solid #ddd;
}

.chat-input {
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.chat-form-buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: flex-end;
}

.chat-form-buttons button {
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.chat-form-buttons button[type="submit"] {
  background-color: #007bff;
  color: #fff;
}

.chat-form-buttons button[type="submit"]:hover:not(:disabled) {
  background-color: #0056b3;
}

.chat-form-buttons button:disabled {
  background-color: #a1c9f1;
  cursor: not-allowed;
}

.chat-form-buttons button[type="button"] {
  background-color: #f44336;
  color: #fff;
}

.chat-form-buttons button[type="button"]:hover {
  background-color: #d32f2f;
}

/* subtle branding text */
.branding {
  font-size: 0.75rem;
  color: #888;
  text-align: left;
  margin-top: 0.5rem;
}

/* FAQ STYLES */
.faq-divider {
  margin: 12px 0 6px;
  font-weight: bold;
  font-size: 14px;
  color: #555;
  padding-left: 4px;
}

.faq-buttons {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin: 16px 0;
}

.faq-button {
  padding: 10px 14px;
  border: none;
  background-color: #f5f5f5;
  border-radius: 6px;
  cursor: pointer;
  text-align: left;
  font-size: 15px;
  transition: background-color 0.2s ease;
}

.faq-button:hover {
  background-color: #e0e0e0;
}
